<template>
	<section>
		<div class="inr-banner">
			<img :src="this.imgUrlBanner" alt=""/>
		</div>
		 <div class="encyc-list">
		<div class="inner">
       <div class="inf-info">当前位置：<a href="javascript:;" @click="getList()">首页 &gt;</a><a href="javascript:;" @click="getList()">石油百科 &gt;</a><span class="inf-info-tet"> 正文</span></div>
       <div class="inf-content clearfix">
         <div class="inf-cont-left">
           <div class="cont-left-title">{{contentData.title}}</div>
           <div class="left-txt-tiao clearfix spac">
              <span class="nr-ico-txt"><i class="c-ico inf-nr-ico"></i>作者：{{contentData.author}}</span>
              <span class="nr-ico-txt"><i class="c-ico inf-nr-icol"></i>浏览次数：{{contentData.browseCount}}</span>
			  <span class="nr-ico-txt" v-if="contentData.origin != null && contentData.origin != ''"><i class="c-ico inf-nr-icoy"></i>来源：{{contentData.origin}}</span>
              <span class="nr-ico-txt"><i class="c-ico inf-nr-icor"></i>{{contentData.releaseDate}}</span>
            </div>
            <div class="cont-left-text">
             <p v-html="contentData.textHtml"></p> 
            </div>
        
           <!-- <div class="text-sxwz">
           <div  v-if="contentData.preUuid != ''" >
           	上一篇：<a href="javascript:;" class="cont-left-pic" @click="goweb(contentData.preUuid)">{{contentData.preTitle}}</a>
           </div>
           <div v-if="contentData.nextUuid != ''" >
           	下一篇：<a href="javascript:;" class="cont-left-pic" @click="goweb(contentData.nextUuid)">{{contentData.nextTitle}}</a>
          </div>
          </div>-->
            <div class="text-sxwz">
	            <div v-if="showPre">
	           		上一篇：<a href="javascript:;" @click="goweb(contentData.preUuid)">{{contentData.preTitle}}</a>
	            </div>
	            <div v-if="showNext">
	            	下一篇：<a href="javascript:;" @click="goweb(contentData.nextUuid)" > {{contentData.nextTitle}}</a>
	            </div>
	          </div>
         </div>
         
          <!--<div class="inf-cont-right">
            <div class="cont-right-yybg">
			<div v-for="item in getClassify" class="bk-fl-list clearfix" >
				<p class="curr" v-if="item.subList.length!=0">{{item.channelMName}}</p>
				<ul v-for="val in item.subList" >
					<li><a href="javascript:;" @click="clickgetClassifyConten($event)">{{val.channelName}}</a></li>
				</ul>
				</div>
            </div>
         </div>-->
         
         
         <div class="inf-cont-right">
		 	<div class="cont-right-yybg"  v-for="item in getClassify">
	            <h1><a href="javascript:;" @click="getList(item.channelMName)"><i class="c-ico inf-yybg-ico-bk"></i>{{item.channelMName}}</a></h1>
	            <ul class="bk-fl-list clearfix">
					<ul v-for="val in item.subList">
					<!-- <li v-for="val in item.subList"><a href="javascript:;" @click="getListChild(val.channelName)">{{val.channelName}}</a></li> -->
					<li v-if="val.channelName.length <=9"><a href="javascript:;" @click="getListChild(val.channelName)" >{{val.channelName}}</a></li>
					<li v-if="val.channelName.length >9"><a href="javascript:;" @click="getListChild(val.channelName)" :title="val.channelName">{{val.channelName | truncate(9)}}</a></li>
					</ul>
				</ul>
            </div>
         </div>
         
         
         
         
         
         
       </div>
    </div>
	</div>
	
	</section>
</template>

<script>
	import {listData,getHome ,listDataParticulars,petroleumHome,queryCarousels} from '../../api/info';
	export default {
		data() {
			return {
				imgUrlBanner:'',
				showPre:false,
				showNext:false,
				contentData:[],
				getClassify:[],
				NullObj:false               //为空的话
			}
		},
		methods: {
			//右侧列表
			getData(){
				petroleumHome().then((res)=>{
			      	this.getClassify=res.datas;
				})
			},
			
			//传递过来的参数
			 getContenData(){
			 	var contenId =window.localStorage.getItem('BKuuid')
			 	var data ={
			 		"infoUuid":contenId
			 	}
			 	getHome(data).then((res)=>{
			 		if(res.status==9999){
				 			return false;
				 		}
			 		this.contentData=res.datas;
			       if(res.datas.preUuid != null && res.datas.preUuid != ''){
				 	   this.showPre=true;
				 	}else{
				 	   this.showPre=false;
				 	}
				    if(res.datas.nextUuid != null &&  res.datas.nextUuid != ''){
				 	   this.showNext=true;
				 	}else{
				 	   this.showNext=false;
				    }	
			 	})
			   },
			   goweb(idStr){
 			 	var data ={
			 		"infoUuid":idStr
			 	}
			 	getHome(data).then((res)=>{
			 		if(res.status==9999){
			 			return false;
			 		}
			 		this.contentData=res.datas;
			 		if(res.datas.preUuid != null && res.datas.preUuid != ''){
			 			this.showPre=true;
			 		}else{
			 			this.showPre=false;
			 		}
			 		if(res.datas.nextUuid != null &&  res.datas.nextUuid != ''){
			 			this.showNext=true;
			 		}else{
			 			this.showNext=false;
			 		}	
			   })
 			},
 			//跳回列表页
			getList(str){
				this.$router.push({
 			 		path:'/encyclopedia'
 			    });
			},
			getListChild(str){
				this.$router.push({
 			 		path:'/encyclopedia?channelName='+str
 			    });
			},
			getBanner(){
				queryCarousels({typeCode:"WEB",pageCode:"zx_index_barner",pageNum:1,pageSize:10}).then((res)=>{
					this.imgUrlBanner = res.datas[0].imageUrl
				});
			}
		},             
		created(){
         this.getContenData();
		 this.getData();
		 this.getBanner()
		 
		},
		mounted(){
			 document.title= this.contentData.title+"_石油百科_石油知识_买原油网";
		}
	}

</script>
<style type="text/css">
.inf-yybg-ico-bk{background-position:-147px -241px !important; width:17px; height:17px; margin-right:5px;}
.cont-left-text td{padding: 5px 10px;border: 1px solid #DDD;}
.cont-left-text table{width:700px !important;}
.cont-left-text {overflow: hidden;}
/*.cont-left-text img{width: 870px; height: 670px;}*/ 
</style>


